.image_banner + .basic-page {
  margin-top: 80px;
  .inner.narrow {
    max-width: 615px;
    text-align: center;
  }
}

.page-title {
  margin-bottom: 24px;
}

.events {
  max-width: 1120px;
  margin-top: 40px;
  .event {
    background-color: #F3F2F0;
    position: relative;
    margin: 40px 0;
    position: relative;
    z-index: 1;
    &:after, &:before {
      width: 60px;
      // width: 5.52995%;
      height: 60px;
      position: absolute;
      top: -30px;
      content: " ";
      border-radius: 50%;
      background-color: white;
      display: block;
      right: -30px;
      z-index: 2;
    }
    &:before {
      left: - 30px;
      right: auto;
    }
    .bottoms {
      &:after, &:before {
        width: 60px;
        // width: 5.52995%;
        height: 60px;
        position: absolute;
        bottom: -30px;
        content: " ";
        border-radius: 50%;
        background-color: white;
        display: block;
        right: -30px;
        z-index: 2;
      }
      &:before {
        left: - 30px;
        right: auto;
      }
    }
    .intro {
      font-size: 112.5%;
      padding-top: 16px;
      border-top: 1px solid var(--color-foreground);
      margin-top: 0;
    }
    .rte {
      margin: 0 0 16px 0;
      font-size: 93.25%;
      @media screen and (min-width: 768px) {
        margin: 16px 0;
      }
    }
    .flex {
        padding: 48px 16px;
        @media screen and (min-width: 768px) {
        padding: 48px;
        flex-wrap: nowrap;
      }
    }
    .first, .second {
      flex: 1 1 100%;
      h2 {
        font-size: 320%;
      }
    }
    .first {
      @media screen and (min-width: 768px) {
        picture {
          display: none;
          visibility: hidden;
        }
        flex: 1 1 42%;
        padding-right: 24px;
      }
    }
    .second {
      @media screen and (min-width: 768px) {
        flex: 1 1 58%;
      }
      b {
        display: block;
        font-weight: 600;
      }
      @media screen and (max-width: 768px) {
        > picture {
          display: none;
          visibility: hidden;
        }
        display: flex;
        flex-direction: row; 
        flex-wrap: wrap; 
        justify-content: center; 
        align-items: stretch; 
        align-content: stretch;
        .rte {
          order: 2;
        }
        .flex {
          order: 1;
          margin-bottom: 24px;
        }
      }
      .flex {
        padding: 0;
        flex-wrap: nowrap;
        align-items: flex-end;
        .details {
          flex: 1 1 55%;
          
        }
        .ticket {
          text-align: center;
          flex: 1 1 45%;
          align-self: stretch;
          min-height: 83px;
          position: relative;
          .shopify-buy-frame--product {
            position: absolute;
            top: 50%;
            right: 12px;
            height: 53px;
            left: 12px;
            z-index: 5;
            transform: translateY(-50%);
          }
          .cta {
            overflow: hidden;
            position: relative;
            z-index: 1;
            height: 100%;
            flex-wrap: nowrap;
            text-align: center;
            .but {
              display: flex;
              align-items: center;
              justify-content: center;
              flex-wrap: wrap;
              align-content: center;
              height: 100%;
            }
            b {
              flex: 1 1 100%;
              align-self: center;
            }
            .liner {
              display: block;
              position: absolute;
              border: 1px solid var(--color-foreground);
              left: 0px;
              right: 0px;
              top: 0px;
              bottom: 0px;
              z-index: 1;
            }
            &:after, &:before {
              content: " ";
              position: absolute;
              left: -12px;
              width: 24px;
              border-radius: 50%;
              height: 24px;
              border: 1px solid var(--color-foreground);
              background-color: #F3F2F0;
              top: calc(50% - 12px);
              z-index: 3;
            }
            &:after {
              left: auto;
              right: -12px;
            }
          }
        }
      }
    }
  }
}

section.basic-page {
  .narrow {
    h1 {
      margin: 24px 0 32px 0;
      position: relative;
      text-align: center;
      &:after {
        content: " ";
        position: absolute;
        left: -16px;
        bottom: 0;
        width: calc(100% + 32px);
        display: block;
        height: 1px;
        background-color: var(--color-base-text)
      }
    }
  }
  &+.events_area {
    > .events {
      border-top: 1px solid var(--color-foreground);
    }
  }
}

.shopify-section.page-section {
  margin: 80px 0;
}